*{
    font-family: Arial, Helvetica, sans-serif;
  }
  #contact {
    padding-top: 2rem;
    padding-bottom: 4rem;
    padding: 0 2rem;
    margin-bottom: 1rem;
  }
  
  #contact h4 {
    text-align: center;
    font-size: 3rem;
    /* font-family: 'Rajdhani', sans-serif; */
  }
  #contact h2 {
    font-size: 3rem;
    margin-top: 0.8rem;
  }
  #contact p {
    font-size: 1.6rem;
    margin-top: 0.6rem;
  }
  .contact-box {
    display: flex;
    justify-content: space-between;
    margin-top: 1.4rem;
  }
  
  .contact-h3 {
    font-size: 1.6rem;
    color: #c12020;
    font-weight: bold;
  }
  #contact h4{
    margin-top: 3rem;
    margin-bottom: 3rem;
  }
  
  .contact-icon-div {
    display: flex;
    margin-top: 0.5rem;
  }
  .fa-brands {
    margin-right: 1.6rem;
    font-size: 1.75rem;
    color: #fff;
    padding: 0.3rem;
  }
  
  .contact-icon-div a :hover {
    margin-right: 1.6rem;
    font-size: 1.75rem;
    color: rgba(255, 255, 255, 0.8);
    padding: 0.3rem;
    transform: scale(1.125);
  }
  
  .contact-mail {
    display: flex;
    padding: 1.25rem 0;
    width: 70%;
    justify-content: space-between;
  }
  .mail {
    font-size: 1.25rem;
  }
  .mail strong {
    font-weight: bold;
  }
  .tel {
    font-size: 1.25rem;
  }
  .tel strong {
    font-weight: bold;
  }
  
  .resume {
    display: flex;
    width: 90%;
    margin: 1.35rem 0;
    justify-content: space-between;
  }
  
  .resume-box {
    padding: 0.3rem;
  }
  .resume-box a {
    font-size: 1.15rem;
  }
  .view-box a {
    font-size: 1.15rem;
  }
  
  .resume-box :hover {
    background-color: white;
    color: black;
    padding: 0.3rem;
    transform: scale(1.125);
    border-radius: 3px;
  }
  
  .view-box {
    padding: 0.3rem;
    margin-right: 0.25rem;
    border-radius: 3px;
    color: white;
    border: 1px solid white;
  }
  .view-box:hover {
    background-color: white;
    color: black;
    border: 1px solid black;
  }
  
  .view-box:hover a {
    background-color: white;
    color: black;
  }