/* *{
  font-family: Arial, Helvetica, sans-serif;
} */
#nav-menu{
    position: relative;
    padding: 0;
    margin: 0;
  
  }
  #sticky{
    position: fixed;
    top: 0.1px;
    background-color: rgb(0,0,0);
    z-index: 100;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
  }
  header {
    padding: 1rem 2rem;
  }
  
  .main-brand {
    font-size: 1.5rem;
  }
  
  .main-brand strong {
    font-size: 3rem;
    font-family: 'Tangerine', cursive;
  }
  
  #toggle {
    display: none;
  }
  
  .bar {
    display: none;
  }
  
  .nav .list {
    margin-left: 2rem;
  }
  .nav .list :hover {
    border-bottom: 2px solid cornflowerblue;
    transition: border-bottem 0.6s linear;
  }
  
  .nav .active {
    border-radius: 0.5rem;
    padding: 0.6rem;
    position: relative;
    margin-left: 2rem;
    overflow: hidden;
  }
  
  .nav .active:hover {
    border: transparent;
  }
  
  .nav .active span:nth-child(1) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: -webkit-gradient(
      linear,
      left top,
      right top,
      from(#0c002b),
      to(#f80202)
    );
    background: linear-gradient(to right, #0c002b, #f80202);
    -webkit-animation: animate1 2s linear infinite;
    animation: animate1 2s linear infinite;
  }
  
  @-webkit-keyframes animate1 {
    0% {
      -webkit-transform: translateX(-100%);
      transform: translateX(-100%);
    }
    100% {
      -webkit-transform: translateX(100%);
      transform: translateX(100%);
    }
  }
  
  @keyframes animate1 {
    0% {
      -webkit-transform: translateX(-100%);
      transform: translateX(-100%);
    }
    100% {
      -webkit-transform: translateX(100%);
      transform: translateX(100%);
    }
  }
  
  .nav .active span:nth-child(2) {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 2px;
    background: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(#0c002b),
      to(#f80202)
    );
    background: linear-gradient(to bottom, #0c002b, #f80202);
    -webkit-animation: animate2 2s linear infinite;
    animation: animate2 2s linear infinite;
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
  }
  
  @-webkit-keyframes animate2 {
    0% {
      -webkit-transform: translateY(-100%);
      transform: translateY(-100%);
    }
    100% {
      -webkit-transform: translateY(100%);
      transform: translateY(100%);
    }
  }
  
  @keyframes animate2 {
    0% {
      -webkit-transform: translateY(-100%);
      transform: translateY(-100%);
    }
    100% {
      -webkit-transform: translateY(100%);
      transform: translateY(100%);
    }
  }
  
  .nav .active span:nth-child(3) {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: -webkit-gradient(
      linear,
      right top,
      left top,
      from(#0c002b),
      to(#f80202)
    );
    background: linear-gradient(to left, #0c002b, #f80202);
    -webkit-animation: animate3 2s linear infinite;
    animation: animate3 2s linear infinite;
  }
  
  @-webkit-keyframes animate3 {
    0% {
      -webkit-transform: translateX(100%);
      transform: translateX(100%);
    }
    100% {
      -webkit-transform: translateX(-100%);
      transform: translateX(-100%);
    }
  }
  
  @keyframes animate3 {
    0% {
      -webkit-transform: translateX(100%);
      transform: translateX(100%);
    }
    100% {
      -webkit-transform: translateX(-100%);
      transform: translateX(-100%);
    }
  }
  
  .nav .active span:nth-child(4) {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 2px;
    background: -webkit-gradient(
      linear,
      left bottom,
      left top,
      from(#0c002b),
      to(#f80202)
    );
    background: linear-gradient(to top, #0c002b, #f80202);
    -webkit-animation: animate4 2s linear infinite;
    animation: animate4 2s linear infinite;
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
  }
  
  @-webkit-keyframes animate4 {
    0% {
      -webkit-transform: translateY(100%);
      transform: translateY(100%);
    }
    100% {
      -webkit-transform: translateY(-100%);
      transform: translateY(-100%);
    }
  }
  
  @keyframes animate4 {
    0% {
      -webkit-transform: translateY(100%);
      transform: translateY(100%);
    }
    100% {
      -webkit-transform: translateY(-100%);
      transform: translateY(-100%);
    }
  }
  
  .nav a {
    font-size: 1.1rem;
    font-family: 'Ubuntu', sans-serif;
  }