*{
    font-family: Arial, Helvetica, sans-serif;
  }
  .hero {
    padding-bottom: 10rem;
    margin-top: 7.8%;
  }
  
  .hero figure {
    width: 35%;
  }
  
  .hero figure img {
    border-radius: 50%;
  }
  
  .hero h1 {
    /* font-family:sans-serif; */
    text-transform: uppercase;
    font-size: 1.5rem;
    padding-top: 6rem;
  }
  
  .hero h1 p {
      display: inline-block;
      font-size: 2rem;
      -webkit-animation: anime 1.5s linear infinite;
    animation: anime 1.5s linear infinite;
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
  }
  
  @keyframes anime {
      0% {
        transform: rotate(0deg);
    
      }
      50% {
        transform: rotate(-45deg);
       
      }
      100% {
        transform: rotate(0deg);
    
      }
    }
    
  
  .hero h2 {
    /* font-family: 'Poiret One', cursive; */
    font-size: 3.5rem;
    padding-top: 1.7rem;
  }
  
  .hero h2 small {
    padding-top: 0.8rem;
    font-size: 3.2rem;
    /* font-family: 'Ubuntu', sans-serif; */
    font-weight: bolder;
  }
  
  .hero img {
    width: 100%;
  }
  
  .hero h3 {
    padding-top: 1rem;
    width: 0ch;
    white-space: nowrap;
    overflow: hidden;
    /* font-family: 'Rajdhani', sans-serif; */
    letter-spacing: 1px;
    font-size: 1.3rem;
    -webkit-animation: text 4s steps(28) infinite alternate;
    animation: text 4s steps(28) infinite alternate;
  }
  
  .hero h3 i {
    color: #c12020;
    font-size: 1.3rem;
    margin-bottom: -5px;
  }
  
  .hero .social-icon {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    width: 2rem;
    height: 2rem;
    color: black;
    background-color: white;
    margin-top: 1rem;
    margin-right: 1rem;
    font-size: 1.2rem;
    border-radius: 50%;
    -webkit-transition: 0.5s linear;
    transition: 0.5s linear;
  }
  
  .hero .social-icon:hover {
    -webkit-transform: rotate(360deg) scale(1.2);
    transform: rotate(360deg) scale(1.2);
    background-color: black;
    color: white;
  }
  
  @-webkit-keyframes text {
    0% {
      width: 0ch;
    }
    100% {
      width: 28ch;
    }
  }
  
  @keyframes text {
    0% {
      width: 0ch;
    }
    100% {
      width: 28ch;
    }
  }